body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: #61dafb;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contact-page {
  min-height: 80vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.leek-header .collapse {
    justify-content: flex-end;
    -webkit-justify-content: flex-end;
}

.navbar-brand {
    text-transform: uppercase;
}

.nav-link {
    text-transform: uppercase;
}

.intro-wrapper {
    background-color: #39cbcc;
    border-bottom: 1px solid #39cbcc;
    color: #fff;
    padding: 30px 0; 
}

.intro-wrapper .avatar-wrap {
    width: 20%;
    padding-right: 30px;
    border-right: 1px solid #fff;
    text-align: center;
}

.intro-wrapper .intro {
    width: 80%;
    padding-left: 50px;
}

.intro-wrapper .contact {
    margin-top: 15px;
}

.intro-wrapper .contact ul {
    padding: 0;
    list-style: none;
}

.intro-wrapper .contact ul li {
    margin-bottom: 10px;
}

.intro-wrapper .contact span {
    font-weight: 700;
}

.intro-wrapper .title {
    display: block;
    font-weight: bold;
    font-size: 24px;
    margin-bottom: 10px;
}

.intro-wrapper p {
    margin: 0;
    line-height: 1.5;
}

.leek-avatar {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    border-radius: 50%;
    max-width: 150px;
    margin-bottom: 15px;
    pointer-events: none;
}

.intro-wrapper h1 {
    color: #fff;
    text-transform: uppercase;
    margin: 0;
}

.cv-content h3 {
    margin-bottom: 50px;
}
.cv-content h3 span {
    padding: 10px 20px;
    background-color: #39cbcc;
    color: #fff;
    position: relative;
    display: inline-block;
    min-width: 200px;
}

.cv-content h3 span::before {
    content: '';
    display: block;
    position: absolute;
    bottom: -10px;
    left: 15px;
    width: 0px;
    height: 0px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #39cbcc;
}

.cv-content table th {
    min-width: 100px;
    padding: 10px;
    color: #39cbcc;
}

.cv-content table td span {
    font-weight: bold;
}

.cv-content table td p {
    line-height: 1.5;
}

.content-top {
    padding: 50px 0;
}

.content-bottom {
    padding: 30px 0;
    background-color: #39cbcc;
}

.cv-content .content-bottom h3 span {
    background-color: #fff;
    color: #39cbcc;
}

.cv-content .content-bottom h3 span::before {
    border-top-color: #fff;
}

.skill-wrap ul {
    padding: 0;
    list-style: none;
}

.skill-wrap ul h4 {
    color: #fff;
    font-size: 1rem;
    font-weight: 400;
}

.skill-wrap ul li {
    margin-bottom: 15px;
}

.skill-wrap .progress {
    position: relative;
    padding: 6px;
    border-radius: 3px;
    border: 2px solid #fff;
    display: block;
    height: auto;
    background-color: transparent;
}

.skill-wrap .progress-bar {
    background-color: #fff;
    display: block;
    height: 100%;
    padding-left: 20px;
    color: #363636;

}

.hobby-wrap {
    padding-left: 50px;
}

.hobby-wrap ul {
    padding-left: 15px;
    color: #fff;
}

.hobby-wrap ul li {
    margin-bottom: 10px;
}

